<template>
  <div class="user-table-select">
    <el-dialog
        title="填入模板名称"
        :visible.sync="visible"
        :before-close="doCancel">
      <div class="salary">
        <div class="item">
          <label>模板名称</label>
          <div>
            <el-input v-model="templateName" placeholder="模板名称"></el-input>
          </div>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="doCancel">取 消</el-button>
        <el-button type="primary" @click="doConfirm">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    name: "template-name",
    data() {
      return {
        templateName: ''
      }
    },
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      name: {
        type: String,
        default: ''
      }
    },
    watch :{
      name : function() {
        this.templateName = this.name
      }
    },
    methods: {
      doCancel: function () {
        this.$emit('update:visible', false)
      },
      doConfirm: function () {
        if (!this.templateName) {
          this.$message.error('请填入模板名称')
          return;
        }
        this.$emit('name:update', this.templateName)
        this.doCancel()
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../common/stylus/variable.styl"
  .salary {
    .item {
      label {
        line-height 40px;
      }
    }
  }
</style>