<template>
  <div class="user-table-select">
    <el-dialog
        title="选择薪酬项目"
        :visible.sync="visible"
        :before-close="doCancel">
      <div class="fill-info">
        <div class="title">项目类型说明：</div>
        <div class="info">
          <p>1.基本项目是指员工信息，计税结果等信息</p>
          <p>2.薪酬项目是指税法规定的支付项目</p>
        </div>
      </div>
      <el-tabs v-model="activeName" type="border-card">
        <el-tab-pane label="薪酬项目" name="first">
          <a @click.stop="selectSalary" :data-id="item.index" class="salary" v-for="item in this.salarys"
             v-if="item.salaryType == 'PAY_ITEM'">
            <span class="name">{{item.name}}</span>
            <span class="descs">{{item.descs}}</span>
          </a>
        </el-tab-pane>
        <el-tab-pane label="基本项目" name="second">
          <a @click.stop="selectSalary" :data-id="item.index" class="salary" v-for="item in this.salarys"
             v-if="item.salaryType != 'PAY_ITEM'">
            <span class="name">{{item.name}}</span>
            <span class="descs">{{item.descs}}</span>
          </a>
        </el-tab-pane>
      </el-tabs>
    </el-dialog>
  </div>
</template>

<script>
  import SalaryApi from "@/api/manage/salary";

  export default {
    name: "salary-select",
    data() {
      return {
        activeName: 'first',
        salarys: []
      }
    },
    props: {
      visible: {
        type: Boolean,
        default: false
      },
    },
    mounted() {
      this.loadSalarys();
    },
    methods: {
      doCancel: function () {
        this.$emit('update:visible', false)
      },
      loadSalarys() {
        SalaryApi.list().then(res => {
          this.salarys = res.obj;
        })
      },
      selectSalary: function (e) {
        var id = e.currentTarget.getAttribute("data-id");
        var salary = null;
        for (var i = 0; i < this.salarys.length; i++) {
          if (this.salarys[i].index == id) {
            salary = this.salarys[i];
            break;
          }
        }
        this.$emit('update:visible', false)
        this.$emit('select:salary', salary)
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../common/stylus/variable.styl"
  .fill-info {
    border 1px solid #ddd;
    padding 10px;
    margin-bottom 15px;
    box-shadow 1px 2px 2px #ddd;
    .title {
      padding 10px 0;
      font-weight bold;
      margin 0;
    }
    .info {
      p {
        line-height 30px;
      }
    }
  }
  
  .salary {
    display block;
    border: 1px solid #ddd;
    margin-bottom 10px;
    padding 10px;
    .name {
      display block;
      padding-bottom 10px;
      font-weight bold;
    }
    .descs {
      display block;
      color $color-light-grey;
    }
  }
</style>