<template>
  <div class="user-table-select">
    <el-dialog
        :title="'薪酬项目： ' + this.salary.name"
        :visible.sync="visible"
        :before-close="doCancel">
      <div class="salary">
        <div class="item">
          <label>项目名称</label>
          <div>{{this.salary.name}}</div>
        </div>
        <div class="item">
          <label>项目描述</label>
          <div>{{this.salary.descs}}</div>
        </div>
        <div class="item" v-if="this.salary.payItem">
          <label>税法解释</label>
          <div>{{this.salary.payItem.subjectDesc}}</div>
        </div>
        <div class="item" v-if="this.salary.payItem">
          <label>税法依据</label>
          <div>{{this.salary.payItem.subjectTax}}</div>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import SalaryApi from "@/api/manage/salary";

  export default {
    name: "salary-view",
    data() {
      return {
        salary: {}
      }
    },
    props: {
      visible: {
        type: Boolean,
        default: false
      },
      id: {
        type: String,
        default: null
      }
    },
    watch: {
      id: function () {
        this.getSalary()
      }
    },
    methods: {
      getSalary: function () {
        if (this.id) {
          SalaryApi.get(this.id).then(res => {
            this.salary = res.obj;
          })
        }
      },
      doCancel: function () {
        this.$emit('update:visible', false)
      },
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
  @require "../../common/stylus/variable.styl"
  .salary {
    .item {
      label {
        padding 10px;
        line-height 40px;
        font-weight bold;
      }
      div {
        padding 10px;
        line-height 20px;
        color $color-light-grey;
      }
    }
  }
</style>